// 初始化
$(function() {
  getActivePage()
  initPageActive()
  onPageActive()
  initContentShow()
  onHouseFeatureHover()
  onHomeFeatureHover()
  setHomeProcessActiveInterval()
  onHomeProcessHover()
  onCompanyFeatureHover()
  onCompanyDesignHover()
})


// active状态的页面
var activePage = location.hash
// 全屋定制流程模块active索引
var activeHomeProcessIndex = 0
// 全屋定制流程模块interval定时器
var homeProcessIntervalTimer = null
// 全屋定制流程模块timeout定时器
var homeProcessTimeoutTimer = null

// 获取active状态的页面
function getActivePage() {
  var pageList = []
  $('.solution__page-item').each(function() {
    pageList.push($(this).attr('href'))
  })
  if(!activePage || pageList.indexOf(activePage) < 0) {
    activePage = $($('.solution__page-item')[1]).attr('href')
  }
}
// 初始化页面active状态
function initPageActive() {
  resetPageActive()
  $('.solution__page-item').each(function() {
    if($(this).attr('href') === activePage) {
      $(this).addClass('active')
    }
  })
}
// 重置页面active状态
function resetPageActive() {
  $('.solution__page-item').removeClass('active')
}
// 监听页面active状态
function onPageActive() {
  $('.solution__page-item').click(function() {
    resetPageActive()
    $(this).addClass('active')
    resetContentShow()
    $(changeIdSelectorToClassSelector($(this).attr('href'))).show()
    $('.solution__banner').css({
      background: 'url(' + $(changeIdSelectorToClassSelector($(this).attr('href'))).data('banner') + '?x-oss-process=image/resize,m_fill,w_1920,h_400/format,jpg/interlace,1) no-repeat 50% 50%/auto 100%'
    })
  })
}

// 初始化内容展示状态
function initContentShow() {
  resetContentShow()
  if(activePage) {
    $(changeIdSelectorToClassSelector(activePage)).show()
    $('.solution__banner').css({
      background: 'url(' + $(changeIdSelectorToClassSelector(activePage)).data('banner') + '?x-oss-process=image/resize,m_fill,w_1920,h_400/format,jpg/interlace,1) no-repeat 50% 50%/auto 100%'
    })
  }
}
// 重置内容展示状态
function resetContentShow() {
  $('.solution__content').hide()
}

// 监听定制家居特性模块hover状态
function onHouseFeatureHover() {
  $('.house__feature-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetHouseFeatureHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}
// 重置定制家居特性模块hover状态
function resetHouseFeatureHover() {
  $('.house__feature-item').removeClass('hover')
}

// 监听全屋定制特性模块hover状态
function onHomeFeatureHover() {
  $('.home__feature-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetHomeFeatureHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}
// 重置全屋定制特性模块hover状态
function resetHomeFeatureHover() {
  $('.home__feature-item').removeClass('hover')
}

// 设置全屋定制流程模块active循环定时器
function setHomeProcessActiveInterval() {
  setHomeProcessActive(activeHomeProcessIndex)
  homeProcessIntervalTimer = setInterval(() => {
    activeHomeProcessIndex += 1
    setHomeProcessActive(activeHomeProcessIndex)
    if(activeHomeProcessIndex >= 4) {
      activeHomeProcessIndex = -1
    }
  }, 3000)
}
// 设置全屋定制流程模块active状态
function setHomeProcessActive(index) {
  resetHomeProcessActive()
  $($('.home__process-item')[index]).addClass('active')
  $('.home__process-image-' + (index + 1)).addClass('active')
}
// 重置全屋定制流程模块active状态
function resetHomeProcessActive() {
  $('.home__process-item').removeClass('active')
  $('.home__process-image img').removeClass('active')
}
// 监听全屋定制流程模块hover状态
function onHomeProcessHover() {
  $('.home__process-item').on('mouseover', function() {
    clearInterval(homeProcessIntervalTimer)
    activeHomeProcessIndex = $(this).index()
    setHomeProcessActive(activeHomeProcessIndex)
  })
  $('.home__process-image img').on('mouseover', function() {
    clearInterval(homeProcessIntervalTimer)
    activeHomeProcessIndex = $(this).data('index')
    setHomeProcessActive(activeHomeProcessIndex)
  })
  $('.home__process-item, .home__process-image img').on('mouseout', function() {
    clearTimeout(homeProcessTimeoutTimer)
    homeProcessTimeoutTimer = setTimeout(() => {
      setHomeProcessActiveInterval()
    }, 3000)
  })
}

// 兼容装修公司特性模块hover状态
function onCompanyFeatureHover() {
  $('.company__feature-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetCompanyFeatureHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}
// 重置装修公司特性模块hover状态
function resetCompanyFeatureHover() {
  $('.company__feature-item').removeClass('hover')
}

// 监听装修公司设计模块hover状态
function onCompanyDesignHover() {
  $('.company__design-title').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetCompanyDesignHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}
// 重置装修公司设计模块hover状态
function resetCompanyDesignHover() {
  $('.company__design-title').removeClass('hover')
}

// 切换视频/图片资源
function changeAttach(item) {
  var parentClass = item.prop('className').split(' ')[0].split('-')[0]
  $('.' + parentClass + '-media').hide()
  $('.' + parentClass + '-media').each(function() {
    if($(this).data('index') === item.data('index')) {
      $(this).removeClass('hidden').show()
    }
  })
}
